export default {

	onShareAppMessage(res) {
		return {
			title: '购物先领券，一年省一半',
			path: '/pages/index/index'
		}
	},
	data() {
		return {
			image: 'https://www.gomyorder.cn/erweima.jpg',
			webviewStyles: {
				progress: {
					color: '#FF3333'
				}
			},
			isWeiXin: false
		}
	},
	onLoad() {






		//获取客服二维码
		this.$Request.getT("/common/type/2").then(res => {
			if (res.status == 0) {
				if (res.data && res.data.value) {
					this.image = res.data.value;
				}
				// uni.navigateBack();
			}
		});
	},
	onShow() {









	},
	onPullDownRefresh: function() {
		uni.stopPullDownRefresh(); // 停止刷新
	},
	methods: {
		navBack() {
			uni.navigateBack();
		},
		follow() {
			this.$Request.get("/tao/wx/follow/" + this.$queue.getData("openid")).then(res => {
				if (res === true) {
					window.location.replace(this.$queue.publicYuMing());
				} else {
					uni.showToast({
						title: "请长按识别上方二维码关注公众号",
						mask: false,
						duration: 1500,
						icon: "none"
					});
				}

			});
		},
		rests() {
			uni.showToast({
				title: "已刷新请再次长按识别",
				mask: false,
				duration: 1500,
				icon: "none"
			});
			window.location.reload();
		}
	},

}
